import { Component } from '@angular/core';
import { ToastitService, IToastit, ToastitAnimation, ToastitType, ToastitAlign } from 'projects/toastit/src/public_api';

@Component({
	selector: 'app-root',
	templateUrl: './app.component.html',
	styleUrls: ['./app.component.css'],
})
export class AppComponent {
	public options: IToastit;
	public enterAnimation: string = 'fadeIn';
	public leaveAnimation: string = 'fadeOut';
	private lastId: number;

	public constructor(private toastitService: ToastitService) {
		this.options = {
			align: ToastitAlign.TR,
			type: ToastitType.Success,
			title: 'I am a Tos-tit',
			message: 'I have the power',
			enterAnimation: ToastitAnimation[this.enterAnimation],
			leaveAnimation: ToastitAnimation[this.leaveAnimation],
			timeout: 5
		};
	}
	
	public onAddToastit() {
		if (!this.options.title) { this.options.title = 'What did you expect?'; }
		this.options.enterAnimation = ToastitAnimation[this.enterAnimation];
		this.options.leaveAnimation = ToastitAnimation[this.leaveAnimation];
		this.lastId = this.toastitService.add(this.options);
	}

	public onRemoveAll(){
		this.toastitService.removeAll();
	}

	public onRemoveLast(){
		this.toastitService.remove(this.lastId);
	}

	public getAlign(value){
		var values = value.split('-');
		return values[0][0] + values[1][0];
	}
}
